.btn {
  display: inline-block;
  position: relative;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  color: #fff;
  background: #ea7722;
  border-radius: 5px;
  padding: 15px 20px;
  min-width: 172px;
  text-align: center;
  border: 1px solid #ea7722;
  cursor: pointer;
  transition: all 0.1s ease-in;
}
.btn:focus {
  outline: 0;
}
.btn__container:hover .btn,
.btn:hover {
  background: #fff;
  color: #3d3d3d;
}
.btn__container:active .btn,
.btn:active,
.btn--active {
  background: #ea7722;
  color: #fff;
  box-shadow: inset 0 10px 18px 0 rgba(56,56,56,0.23);
}
.btn__container {
  cursor: pointer;
}
.btnArrow {
  position: relative;
  border-radius: 5px;
  font-family: 'DIN Pro Cond', sans-serif;
  font-size: 18px;
  line-height: 1;
  color: #fff;
  width: 251px;
  height: 76px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-right: 60px;
  border: 0;
  transition: all 0.1s ease-in;
  overflow: hidden;
}
.btnArrow:before,
.btnArrow:after {
  content: '';
  position: absolute;
  z-index: -1;
  transition: all 0.1s ease-in;
}
.btnArrow:before {
  top: 0;
  bottom: 0;
  right: 60px;
  left: 0;
  background: #ea7722;
  border: 1px solid #ea7722;
  border-radius: 5px 0 0 5px;
}
.btnArrow:after {
  content: '';
  position: absolute;
  right: 0px;
  top: 0px;
  width: 60px;
  bottom: 0px;
  background: linear-gradient(to right, #d36b1f, #e67521);
  border-radius: 0 5px 5px 0;
  z-index: -1;
  transition: all 0.1s ease-in;
}
.btnArrow span {
  max-height: 56px;
  overflow: hidden;
}
.btnArrow span:after {
  content: '';
  position: absolute;
  right: 0px;
  top: 0px;
  width: 60px;
  bottom: 0px;
  background: #fff;
  border: 1px solid #ea7722;
  border-left: 0;
  border-radius: 0 5px 5px 0;
  z-index: -1;
  transition: all 0.1s ease-in;
}
.btnArrow svg {
  position: absolute;
  right: 22px;
  top: 50%;
  transform: translateY(-50%);
  fill: #fff;
  width: 17px;
  height: 16px;
  transition: all 0.1s ease-in;
}
.btnArrow input {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 200%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}
.btnArrow:hover {
  color: #262626;
}
.btnArrow:hover:before {
  background-color: #fff;
}
.btnArrow:hover:after {
  opacity: 0;
}
.btnArrow:hover svg {
  fill: #d36b1f;
}
.btnArrow:active {
  color: #fff;
  box-shadow: inset 0 10px 18px 0 rgba(56,56,56,0.23);
  border-color: #c56a27;
}
.btnArrow:active:before {
  background-color: #ea7722;
}
.btnArrow:active:after {
  opacity: 1;
}
.btnArrow:active svg {
  fill: #fff;
}
.btnArrow--line {
  height: 61px;
  width: auto;
  padding: 0 93px 0 26px;
}
.btnArrow--block {
  width: 100%;
}
.btnArrow--outline {
  color: #222;
}
.btnArrow--outline:before {
  background: #fff;
}
.btnArrow--outline:after {
  opacity: 0;
}
.btnArrow--outline svg {
  fill: #d36b1f;
}
.wpcf7-checkbox .wpcf7-list-item,
.checkbox {
  cursor: pointer;
}
.wpcf7-checkbox .wpcf7-list-item input,
.checkbox input {
  display: none;
}
.wpcf7-checkbox .wpcf7-list-item span,
.checkbox span {
  display: block;
  position: relative;
  padding-left: 40px;
  padding-top: 1px;
  font-size: 16px;
  transition: all 0.1s ease-in;
}
.wpcf7-checkbox .wpcf7-list-item span:not(:empty):before,
.checkbox span:not(:empty):before {
  margin-right: 16px;
}
.wpcf7-checkbox .wpcf7-list-item span:before,
.checkbox span:before,
.wpcf7-checkbox .wpcf7-list-item span:after,
.checkbox span:after {
  content: '';
  display: block;
}
.wpcf7-checkbox .wpcf7-list-item span:before,
.checkbox span:before {
  position: absolute;
  top: 0;
  left: 0;
  width: 25px;
  height: 25px;
  border-radius: 3px;
  background: #fff;
  border: 2px solid #a4a4a4;
  transition: all 0.1s ease-in;
}
.wpcf7-checkbox .wpcf7-list-item span:after,
.checkbox span:after {
  position: absolute;
  background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAA0AAAAKCAQAAADF5xxPAAAAPklEQVR42mNgwAr+izDgkND4P/O/KC4JTUISyOaiSoj+n/lfA4dRYAENBI1pvg8WCbCk+f9l/y1w+UURXQQAQI4tMK9BCDsAAAAASUVORK5CYII=");
  top: 8px;
  left: 7px;
  width: 13px;
  height: 10px;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.1s ease-in;
}
.wpcf7-checkbox .wpcf7-list-item input:checked ~ span:after,
.checkbox input:checked ~ span:after {
  opacity: 1;
}
.wpcf7-checkbox .wpcf7-list-item input:checked ~ span:before,
.checkbox input:checked ~ span:before {
  border-color: #ea7722;
  background: #ea7722;
}
.wpcf7-checkbox .wpcf7-list-item:hover span:before,
.checkbox:hover span:before {
  border-color: #ea7722;
}
.wpcf7-checkbox .wpcf7-list-item--warn,
.checkbox--warn {
  color: #e53641;
}
html {
  font-family: 'Montserrat', sans-serif;
  color: #191919;
  font-size: 15px;
  letter-spacing: 0.01em;
  font-weight: 300;
  line-height: 1.8;
  background: #fff;
}
@media screen and (max-width: 767px) {
  html {
    font-size: 12px;
  }
}
@media print {
  @page {
    margin: 1cm;
  }
}
html,
body {
  overflow-x: hidden;
  min-height: 100vh;
}
.c {
  margin-left: auto;
  margin-right: auto;
  max-width: 1170px;
  width: 100%;
}
@media screen and (max-width: 1199px) {
  .c {
    max-width: 748px;
  }
}
@media screen and (max-width: 767px) {
  .c {
    max-width: 100%;
  }
}
.lazy {
  visibility: hidden;
  opacity: 0;
}
.lazy.lazy--visible {
  visibility: visible;
  opacity: 1;
}
img[data-src],
img[data-srcset] {
  min-height: 1px;
}
.hidden {
  display: none;
}
#sprites {
  position: absolute;
  width: 0;
  height: 0;
  top: 0;
  left: 0;
}
.center {
  text-align: center;
}
.small {
  font-size: 0.85rem;
  opacity: 0.5;
}
.texture {
  overflow: hidden;
  position: relative;
}
.texture:after {
  content: '';
  position: absolute;
  width: 1000px;
  height: 1000px;
  top: 50%;
  left: 50%;
  margin-left: -500px;
  margin-top: -500px;
  background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTAiIGhlaWdodD0iMTAiIHZpZXdCb3g9IjAgMCAxMCAxMCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj48cGF0aCBkPSJtMCAwaDV2MTBoLTV6Ii8+PC9zdmc+Cg==");
  background-repeat: repeat;
  background-position: center center;
  opacity: 0.05;
}
.link {
  color: #191919;
  width: auto;
  display: inline-block;
  font-weight: 500;
  letter-spacing: 0.2em;
  border: 0;
  border-left: 2px solid #e33535;
  position: relative;
  padding: 26px 50px 27px 31px;
}
@media screen and (max-width: 767px) {
  .link {
    font-size: 12px;
    padding: 25px 39px 24px 27px;
  }
}
.link:before,
.link:after {
  content: '';
  position: absolute;
  height: 2px;
  background: #e33535;
  left: 0;
  transition: all 0.25s ease-in;
}
.link:before {
  top: 0;
  width: 116px;
}
.link:after {
  bottom: 0;
  width: 39px;
}
.link svg {
  position: absolute;
  width: 23px;
  height: 20px;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  fill: #e33535;
  transition: all 0.1s ease-in;
}
.link:hover {
  color: #191919;
  border-bottom: 0;
}
.link:hover:before {
  width: 39px;
}
.link:hover:after {
  width: 116px;
}
.link:hover svg {
  right: 0;
}
.link--white {
  color: #fff;
}
.link--white:hover {
  color: #fff;
}
.link--sm {
  font-size: 11px;
  padding: 8px 12px 9px 12px;
}
@media screen and (max-width: 767px) {
  .link--sm {
    font-size: 10px;
  }
}
.link--sm svg {
  position: static;
  vertical-align: middle;
  width: 7px;
  height: 7px;
  fill: #191919;
  margin-right: 8px;
  margin-top: 5px;
}
.tags {
  display: flex;
  align-self: center;
  flex-wrap: wrap;
  margin: 0 -6px;
}
@media screen and (max-width: 767px) {
  .tags {
    justify-content: center;
  }
}
.tag {
  border: 1px solid #e0e0e0;
  letter-spacing: 0.2em;
  margin: 5px 6px;
  font-size: 13px;
  font-weight: normal;
  padding: 1px 10px;
  color: #191919;
  transition: all 0.1s ease-in;
}
@media screen and (max-width: 767px) {
  .tag {
    font-size: 10px;
    padding: 6px 15px;
  }
}
.tag:hover {
  color: #191919;
  border-color: #e33535;
}
.tag--active {
  border-color: #e33535;
  background: #e33535;
  color: #fff;
}
.scroll {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: 590px;
  color: #fff;
  font-size: 11px;
  letter-spacing: 0.2em;
  padding: 16px 150px 15px 0;
  transform: rotate(90deg);
  border: 0;
}
@media screen and (max-width: 1199px) {
  .scroll {
    margin-left: 230px;
  }
}
.scroll:before,
.scroll:after {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.1s ease-in;
}
.scroll:before {
  width: 80px;
  height: 1px;
  background: rgba(255,255,255,0.1);
  right: 50px;
}
.scroll:after {
  width: 50px;
  height: 50px;
  border: 1px solid rgba(255,255,255,0.1);
  border-radius: 50%;
  right: 0;
}
.scroll svg {
  width: 17px;
  height: 17px;
  position: absolute;
  fill: #e33535;
  top: 50%;
  right: 16px;
  transform: translateY(-50%) rotate(90deg);
}
.scroll:hover {
  color: #fff;
}
.scroll:hover:before {
  background: #e33535;
}
.scroll:hover:after {
  border-color: #e33535;
}
.nav {
  display: flex;
}
.nav__button {
  position: relative;
  width: 80px;
  height: 80px;
  background: #191919;
}
@media screen and (max-width: 767px) {
  .nav__button {
    width: 70px;
    height: 70px;
  }
}
.nav__button:last-child {
  border-left: 1px solid #303030;
}
.nav__button svg {
  position: absolute;
  fill: #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 19px;
  height: 19px;
  transition: all 0.1s ease-in;
}
.nav__button--next svg {
  transform: translate(-50%, -50%) rotate(180deg);
}
.nav__button:hover svg {
  fill: #e33535;
}
.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.pagination .page-numbers {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 4px;
  width: 60px;
